import React from "react";
// TODO: import useFormik from formik library
import {useFormik} from 'formik';

function App() {
  // TODO: add a const called formik assigned to useFormik()
  const formik = useFormik({
    initialValues: {
      emailField: '',
      pswField: '',
    }, 
    onSubmit: values => {
      console.log('form:', values);
      alert('Login Successful')
    }, 
    validate: values => {
      let errors = {};
      if(!values.emailField) {errors.email = 'Field Required';} else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.emailField)) {
        errors.email = 'Username should be an email';
      }
      if(!values.pswField) errors.password = 'Field Required';
      return errors;
    }
  });
  return (
    <div>
      <form onSubmit={formik.handleSubmit}>
        <div>Email</div>
        <input name="emailField" type="text" onChange={formik.handleChange} value={formik.values.emailField} />
        <div name="emailError">
          {formik.errors.email ? <div style={{color:'red'}}>{formik.errors.email}</div>: null}
        </div>
        <div>Password</div>
        <input name="pswField" type="text" onChange={formik.handleChange} value={formik.values.pswField} />
        <div name="pswError">
        {formik.errors.password ? <div style={{color:'red'}}>{formik.errors.password}</div>: null}
        </div>
        <button name="submitBtn" type="submit">Submit</button>    
      </form>
    </div>
  );
}

export default App;
